import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllEmployeePromotionsComponent } from './promotion/all-employee-promotions/all-employee-promotions.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { AllEmployeeDemotionsComponent } from './demotion/all-employee-demotions/all-employee-demotions.component';
import { AllEmployeeTransfersComponent } from './transfer/all-employee-transfers/all-employee-transfers.component';

const routes: Routes = [
  {
    path: "all-employee-promotions",
    component: AllEmployeePromotionsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "all-employee-demotions",
    component: AllEmployeeDemotionsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "all-employee-transfers",
    component: AllEmployeeTransfersComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeActivitiesRoutingModule { }
